	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Attempted Denial-of-Service Attack","date":"24 Feb 2007","slug":"/2007-02-24-attempted-denial-of-service-attack/","tags":"Y2007"},"html":"<p>Christopher and I are playing bachelor this weekend. Today we decided to go toy and tech shopping in Hamburg. We drove to the Park-and-Ride in Harburg and took the S-Bahn downtown. </p>\n<p>When we returned to drive home, the lock in the drivers' side door was stuck and would only partially turn. The passenger side lock was completely jammed and I could not insert the key. My first thought was that my key was damaged. I was able to open the back hatch, however, so Christopher could climb in and open the doors from the inside. The ignition also switched on with no problems.</p>\n<p>When we got home I tried my spare key with the same results. I can only conclude that a \"friendly\" passerby in the parking ramp decided it would be fun to jam the door locks on a certain VW Lupo. </p>\n<p>I think I still prefer a proper lock with a physical key to the electronic key for MamaMaus' Audi. One time last month she came back in the house after she started her car, and left her key in the kitchen. She was able to drive off, but was then unable to start the car again when she wanted to come home. Good thing she was only at the grocery store, and not some far-away city.</p>"},"next":{"fields":{"title":"My Not So Aching Back","date":"21 Feb 2007","slug":"/2007-02-21-my-not-so-aching-back/"}},"previous":{"fields":{"title":"","date":"26 Feb 2007","slug":"/2007-02-26-lothar-gunther-buchheim/"}}},{"node":{"fields":{"title":"My Not So Aching Back","date":"21 Feb 2007","slug":"/2007-02-21-my-not-so-aching-back/","tags":"Y2007"},"html":"<p>Thanks to <a href=\"http://www.paradox1x.org/weblog/kmartino/archives/004709.shtml\">Karl and everyone else</a> who wished me well for my birthday, despite my post telling everyone not to.</p>\n<p>As for my back, I'm making good progress. As my father phrased it, it no longer hurts, it's just stiff and sore. I can sleep through the night without pain, and today I drove to Hamburg and back without any pain for the first time since November. I must be doing something right.</p>"},"next":{"fields":{"title":"pfSense: M0n0wall and more","date":"20 Feb 2007","slug":"/2007-02-20-pfsense-m0n0wall-and-more/"}},"previous":{"fields":{"title":"Attempted Denial-of-Service Attack","date":"24 Feb 2007","slug":"/2007-02-24-attempted-denial-of-service-attack/"}}},{"node":{"fields":{"title":"pfSense: M0n0wall and more","date":"20 Feb 2007","slug":"/2007-02-20-pfsense-m0n0wall-and-more/","tags":"Y2007"},"html":"<p>I've been a big fan of the FreeBSD-based firewall <a href=\"http://m0n0.ch/wall\">M0n0wall</a> for some time. Yesterday I finally got around to trying out <a href=\"http://www.pfsense.com/\">pfSense</a>, which is based on M0n0wall but which can take advantage of bigger hardware. It's only been a day, but I think I'm hooked.</p>\n<p>M0n0wall was developed for embedded devices, and can work with a minimum of CPU, memory and disk (or CF). That means that it doesn't have some features you might find handy in a firewall, like a login shell or a packet sniffer (<a href=\"http://www.xs4all.nl/~fredmol/m0n0/\">they can be hacked in</a>, but still). M0n0wall does run on x86 hardware, but it's going to <a href=\"http://doc.m0n0.ch/handbook/faq-goals.html\">remain focused on small hardware</a>. pfSense started as a friendly fork a couple of years ago to add features and a more recent FreeBSD for those with hardware to support them. We're not talking massive hardware here... <a href=\"http://www.pfsense.com/index.php?id=43\">128MB RAM and CF</a> (as opposed to <a href=\"http://m0n0.ch/wall/hardware.php\">64MB RAM and 8MB CF</a> for M0n0wall).</p>\n<p>pfSense released 1.0 last fall, but my M0n0walls at home and at work have been working well, so I never looked into it. We're now considering infrasructure changes at work, and it'd be nice to have <a href=\"http://openvpn.org/\">OpenVPN</a> on the firewall. pfSense has it. I run the CD-ROM version of M0n0wall on an old PC at home, with settings saved on a floppy. I inserted the pfSense CD and rebooted. It took over the M0n0wall settings and just worked. Over the console I was able to copy the running system to hard disk, so I can now add packages and install updates.</p>\n<p>Just clicking through the interface I found a half dozen reasons to consider switching even running systems:</p>\n<ul>\n<li>SSH login with standard command-line tools (mmmm, <a href=\"http://en.wikipedia.org/wiki/Tcpdump\">tcpdump</a>)</li>\n<li>OpenVPN on board</li>\n<li>Aliases for groups of hosts, networks and ports</li>\n<li>Add-in packages for <a href=\"http://insecure.org/nmap/\">nmap</a> and <a href=\"http://www.ntop.org/\">ntop</a></li>\n<li>NAT reflector (NAT addresses available from LAN, makes routing <em>much</em> simpler)</li>\n<li>Load Balancer</li>\n</ul>\n<p>The aliases should be real nice since I was raised on the object model in <a href=\"http://www.checkpoint.com/\">CheckPoint</a>. One drawback of M0n0wall is that it's hard to keep the rules organized, the more intelligent aliases in pfSense should make the rules a bit more managable.</p>"},"next":{"fields":{"title":"Fasching, the morning of","date":"19 Feb 2007","slug":"/2007-02-19-fasching-the-morning-of/"}},"previous":{"fields":{"title":"My Not So Aching Back","date":"21 Feb 2007","slug":"/2007-02-21-my-not-so-aching-back/"}}}],"pathPrefix":"","first":false,"last":false,"index":152,"pageCount":1101}}

